import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [

  {
    path: '/urgent_care',
    name: 'Карта помощи новорожденному',
    component: () => import('@/modules/urgent_care/views/UrgentCare.vue')
  },

  {
    path: '/ballard',
    name: 'Шкала Ballard',
    component: () => import('@/modules/ballard/views/Ballard.vue')
  },

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  beforeRouteLeave(to, from, next) {
    const answer = window.confirm('Вы хотите уйти? У вас есть несохранённые изменения!')
    if (answer) {
      next()
    } else {
      next(false)
    }
  },
})

export default router
