    actions: {
        setValue(state, record) {
            let item = state.state.card.items[record.name]
            let values = item.values.split("")
            values[record.id] = record.value
            item.values = values.join("")
        },

        setValueRange(state, record) {
            let item = state.state.card.items[record.name]
            let value = record.value ? record.value : null
            Vue.set(item.values, record.id, value)
        },
    },

    getters: {
        getMother: (state) => {
            return state.mother
        },

        getValues: (state) => (itemName) => {
            let item = state.card.items[itemName]
            return item.values
        },

        getValue: (state) => (record) => {
            let item = state.card.items[record.name]
            let value = item.values[record.id]
            return value
        },

        getItems: (state) => {
            return state.card.items
        },

        getItem: (state) => (name) => {
            return state.card.items[name]
        },

        getApgarItems: (state) => {
            return state.apgar.items
        },

        getApgarValues: (state) => {
            return state.apgar.values
        },

        getApgarLineSum: (state) => (lineIndex) => {
            return state.apgar.values[lineIndex].reduce((accumulator, element) => {
                const value = parseInt(element.value) > 0 ? parseInt(element.value) - 1 : 0
                return accumulator + value
            }, 0)
        },
    },

